.project-details {
    color: white;
    padding: 20px;
  }

.tech-stack-items p {
  margin: 5px 0;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 5px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.tech-stack-items p:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.tech-stack-items p a {
  display: block;
  width: 100%;
  height: 100%;
  padding: 5px 0;
  margin: -5px 0;
}

.tech-stack-items a {
  color: #c0b8d0;
}

.tech-stack-items a:hover {
  text-decoration: underline;
}
  
.project-header h2 {
  text-align: center;
  margin-bottom: 30px;
}

.project-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tech-stack {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.project-link {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.project-link a {
  display: flex;
  align-items: center;
  gap: 10px;
  color: white;
  text-decoration: none;
  transition: color 0.3s;
}

.project-description {
  font-size: 1.2rem;
  line-height: 1.5;
  margin-bottom: 20px;
  width: 50%;
  
}

@media (max-width: 768px) {
  .project-details {
    padding-top: 0px;
  }

  .project-content {
    flex-direction: column;
  }

  .project-header h2 {
    font-size: 1.5rem;
    margin-bottom: 0px;
  }

  .project-description {
    width: 100%;
  }

  /* smaller font for mobile */
  .project-description {
    font-size: 1rem;
  }
}

.project-link a:hover {
  color: #61dafb;
}

.github-text {
  font-size: 1.2rem;
}